///*Заборона прокрутки контенту поза бекдропом при відкритому модальному вікні*/
.is-overflow {
  overflow: hidden;
}

///*Заборона прокрутки контенту при відкритому мобільному меню*/
.is-overflow-mobile {
  @media screen and (max-width: 767px) {
    overflow: hidden;
  }
}
