.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  //  padding: 400px 20px 0px;
  width: 100%;
  height: 100%;
  background: rgba(54, 54, 54, 0.8);
  opacity: 0;
  z-index: 2;
  visibility: hidden;
  pointer-events: none;

  overflow: auto;
  transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 500ms cubic-bezier(0.4, 0, 0.2, 1);

  &::-webkit-scrollbar {
    @media screen and (min-width: 320px) {
      display: none;
    }
  }
}

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal-team {
  position: relative;
  top: 0;
  left: 50%;
  transform: translate(-50%, -18%) scale(0.9);

  padding: 50px;
  min-width: 280px;
  max-width: 440px;
  min-height: 952px;

  font-family: 'Montserrat';
  font-style: normal;
  // object-fit: cover;
  text-align: center;
  background-image: url(/src/images/team-modal/team/teambackground/modalbackground.jpg);
  border-radius: 150px 0px;

  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);

  z-index: 3;
  opacity: 1;
  @media screen and (min-width: 768px) {
    transform: translate(-50%, -15%) scale(0.9);
    max-width: 704px;
  }

  @media screen and (min-width: 1280px) {
    transform: translate(-50%, -20%) scale(0.9);
    max-width: 1034px;
  }
}

