.team-img {
  display: inline-block;
  height: 250px;
  // width: 100%;
  background-size: contain;
  border-radius: 15px;
}

.team-title {
  margin-bottom: 45px;
  color: #dc56c5;
}

.team-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 30px;
  row-gap: 60px;
}

/* Картки працівників */
.team-item {
  overflow: hidden;
  width: 100%;
  background-color: black;
  // box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
  //   0px 2px 1px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 19px 3px rgba(220, 86, 197, 0.55);
    -webkit-box-shadow: 0px 0px 19px 3px rgba(220, 86, 197, 0.55);
    -moz-box-shadow: 0px 0px 19px 3px rgba(220, 86, 197, 0.55);
  border-radius: 50px 0px 50px 4px;
  padding: 5px;

  @media screen and (min-width: 768px) {
    width: calc((100% - 30px) / 2);
  }

  @media screen and (min-width: 1280px) {
    width: calc((100% - 60px) / 3);
  }
}

.team-card-bottom {
  padding: 30px 15px 24px;

  @media screen and (min-width: 1280px) {
    padding: 30px 15px;
  }
}

/* Імена працівників */
.team-item-title {
  margin-bottom: 10px;
  color: #dc56c5;
}

/* Посада */
.team-text {
  color: var(--secondary-text-color);
  font-size: large;
  margin-bottom: 16px;
}

